// common mixins - start

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .5);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
    @include mkdfRelativeHolderLayout(top);
    margin: 53px 0 0 -20px;
    clear: both;
    width: auto;

    @include ipad-landscape {
        margin-left: 0;
    }

    ul {
        @include mkdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        @include ipad-landscape {
            text-align: left;
        }

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 4px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: text-bottom;
                margin: 0;
                padding: 0 10px 6px;
                font-family: $additional-font;
                font-style: italic;
                font-size: 20px;
                line-height: 9px;
            }

            &.mkdf-pag-active,
            &:hover {
                a {
                    color: $first-main-color;
                    background-color: #f2f1ea;
                }
            }

            &.mkdf-pag-prev,
            &.mkdf-pag-next,
            &.mkdf-pag-first,
            &.mkdf-pag-last {
                margin: 0 2px;

                a {
                    font-size: 24px;
                    background-color: transparent;
                    line-height: 17px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.mkdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.mkdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: text-bottom;
                    margin: 0;
                    padding: 0 10px 6px;
                    font-family: $additional-font;
                    font-style: italic;
                    font-size: 20px;
                    line-height: 9px;
                    color: $first-main-color;
                    background-color: #f2f1ea;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;
                        background-color: transparent;


                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                            font-size: 24px;
                            line-height: 12px;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
    font-size: 16px;
    line-height: 2em;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin mkdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 10px 33px;

    } @else if ($size == 'medium') {
        padding: 10px 40px;

    } @else if ($size == 'large') {
        padding: 10px 64px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border: 1px solid transparent $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 #{$input-margin}px;
    padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
    font-family: $additional-font;
    font-size: 18px;
    font-style: italic;
    font-weight: inherit;
    line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    cursor: text;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: #878787;
    }
}

// mixins styles - end

