// animation mixin - start

@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
        @content;
    }

    @keyframes #{$name} {
        @content;
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// heading mixins - start

@mixin mkdfH1() {
    font-size: 59px;
    line-height: 1.1em;
}

@mixin mkdfH2() {
    font-size: 50px;
    line-height: 1.12em;
}

@mixin mkdfH3() {
    font-size: 42px;
    line-height: 1.1em;
}

@mixin mkdfH4() {
    font-size: 35px;
    line-height: 1.1em;
}

@mixin mkdfH5() {
    font-size: 29px;
    line-height: 1.2em;
}

@mixin mkdfH6() {
    font-size: 22px;
    line-height: 1.2em;
}

@mixin mkdfHeadingStyle($with-heading: null, $with-color: true) {

    @if ($with-color) {
        color: $default-heading-color;
    }

    @if ($with-heading == 'h1') {
        @include mkdfH1();
    } @else if ($with-heading == 'h2') {
        @include mkdfH2();
    } @else if ($with-heading == 'h3') {
        @include mkdfH3();
    } @else if ($with-heading == 'h4') {
        @include mkdfH4();
    } @else if ($with-heading == 'h5') {
        @include mkdfH5();
    } @else if ($with-heading == 'h6') {
        @include mkdfH6();
    }
}

@mixin mkdfDefaultHeadingStyle() {
    @include mkdfHeadingStyle();
    font-family: $default-heading-font;
    letter-spacing: -0.02em;
    margin: 25px 0;
    font-weight: 700;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    text-transform: uppercase;

    a {
        @include mkdfTypographyLayout();

        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin mkdfBody() {
    font-family: $default-text-font;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

// heading mixins - end

@mixin mkdfSVGbg($color: $first-main-color) {
    transition: opacity .2s ease-out;

    .mkdf-active-hover-middle {
        position: absolute;
        top: 0;
        left: 0;
        width: 98%;
        height: 100%;
        border-radius: 2px;
        background-color: $color;
    }
    
    svg {
        position: absolute;
        top: 0;
        height: 100%;
        
        polygon {
            fill: $color;
            stroke: $color;
        }
        
        &.mkdf-active-hover-left {
            left: -0.2em;
        }
        
        &.mkdf-active-hover-right {
            right: -0.1em;
        }
    }
}

@mixin mkdfUnderline($color: currentColor, $speed: .6s) {
    width: auto;
    background-repeat: no-repeat;
    background-image: linear-gradient(transparent calc(100% - 2px), $color 2px);
    background-image: -ms-linear-gradient(transparent 95%,currentColor 2px);
    transition: $speed cubic-bezier(0.215, 0.61, 0.355, 1);
    background-size: 0 96%;

    &:hover {
        background-size: 100% 96%;
        color: $color;

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            background-size: 100% 75%;
        }
    }
}

